<template>
	<div class="main">
	    <div class="block"></div>
		<div class="container">
			<titleNavigation titleName="新闻动态"></titleNavigation>
			<ul>
				<news-item v-for="(data, index) in newsList" :newsItem="data" :key="data.id"></news-item>
			</ul>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import titleNavigation from '@/components/titleNavigation'
import newsItem from '@/components/newsItem';

 export default{
 	name:'index',
 	data(){
 		return {
 			newsList:[],
 		}
 	},
 	created(){
 		axios.get('https://www.easy-mock.com/mock/5a0bad8922eff44db04955c0/example_1510714761014/indexData').then(response=>{
 			this.newsList = response.data.data.newsList;
 		})
 	},
 	components: {
 	  titleNavigation,
 	  newsItem,
 	},

 }
</script>

<style scoped>
	.main{
		width:100%;
		background:#f1f1f1;
	}
	.block{
		width:100%;
		height:20px;
		background:#f1f1f1;
	}
	.container{
		width:1200px;
		margin:0px auto;
		background:#ffffff;
	}
	.container ul{
		padding:30px 176px;
	}
	.container ul li{
		position: relative;
		padding-left: 80px;
		min-height: 137px;
		height: auto !important;
		border-bottom: 1px solid #ccc;
		margin-bottom: 10px;
	}
	.time{
		top:21px !important;
	}
</style>


